<template>
  <v-row>
    <v-dialog v-model="dialog" persistent height="100vh" width="80vh">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          fab
          dark
          color="rgba(39,55,70,1)"
          fixed
          right
          bottom
          v-bind="attrs"
          v-on="on"
        >
          <v-icon size="23" color="#D5DBDB" dark>add</v-icon>
        </v-btn>
      </template>
      <v-card color="#F2F4F4" class="white--text">
        <v-container>
          <v-card-title class="text-h5" style="color: #273746">
            Nuevo Tablero
          </v-card-title>
          <v-card-text style="color: #273746">Datos del tablero</v-card-text>

          <v-form ref="form">
            <v-text-field
              class="text-capitalize"
              outlined
              disabled
              v-model="reference"
              label="Referencia"
              required
            ></v-text-field>
            <v-text-field
              outlined
              v-model="nomenclature"
              label="Nomenclatura"
              required
            ></v-text-field>
            <v-text-field
              outlined
              v-model="location"
              label="Localización"
              required
            ></v-text-field>
          </v-form>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="#273746" text @click="dialog = false">
              Cancelar
            </v-btn>
            <v-btn outlined color="#273746" @click="create()"> Crear </v-btn>
          </v-card-actions>
        </v-container>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  name: "BoardNew",
  data() {
    return {
      reference: this.$store.getters.getBoardAll.length + 1,
      nomenclature: "erd",
      location: "palapa",
      dialog: false,
    };
  },
  methods: {
    create() {
      const dt = {
        reference: this.reference,
        nomenclature: this.nomenclature,
        location: this.location,
      };
      console.log(dt);
      this.$store.dispatch("newBoardAction", dt);
      this.dialog = false;
      this.reference = this.referenceAuto();
      this.nomenclature = "";
      this.location = "";
    },
    referenceAuto() {
      return this.$store.getters.getBoardAll.length + 1;
    },
  },
};
</script>

<style scoped>
.code input {
  text-transform: uppercase;
}
</style>